import { React, useEffect, useState } from "react";
import axios from "axios";
import Loader from "../Loader";
import ProductCard from '../ProductCard'
import { useAxiosGet } from "../../Hooks/HttpRequests";
import PaginationComponent from "./PaginationComponent";

function Home(){
    let content = null
    const url = `https://5e9623dc5b19f10016b5e31f.mockapi.io/api/v1/products?page=1&limit=48`
    const [currentPage, setCurrentPage] = useState(1)
    let products = useAxiosGet(url, currentPage)
    if(products.loading){
        content = 
        <Loader/>
    }
    if(products.error){
        content =
        <p>The product does not exist. Please try again with a valid product ID</p>
    }
    if(products.data){
        const dataToDisplay = products.data.slice(products.indexOne, products.indexLast)
        content = 
        dataToDisplay.map((product, key)=>
        <div key= {key}>
            <ProductCard
            product={product}/>
        </div>
        )
    }
    const paginate = (number) => setCurrentPage(number)
    return(
        
        <div 
        className="text-blue-800 font-medium p-1 mt-5 h-screen w-4/5 m-auto text-center">
            <h1 className="font-bold text-2xl mb-5 text-chocolate-800">Best Seller</h1>
            {content}
        <PaginationComponent postPerPage={products.posts} totalPosts={products.totalPosts} paginate={paginate}/>
        </div> 
    )
}
export default Home