import { React, useEffect, useState } from "react";
import axios from "axios";
import { data } from "autoprefixer";

export function useAxiosGet(url, currentPage){
    const [request, setRequest] = useState({
        loading : false,
        data : null,
        error:false
    })
    const [postsPerPage, setPostsPerPage] = useState(8)
    const indexOfLastPost = currentPage*postsPerPage;
    const indexOfFirstPost = indexOfLastPost-postsPerPage
    useEffect(()=>{
        setRequest({
            loading:true,
            data:null,
            totalPosts:null,
            error:false,
            posts: null
        })
        axios.get(url)
        .then(response=>{
            setRequest({
                loading:false,
                data:response.data,
                totalPosts:response.data.length,
                error:false,
                posts: postsPerPage,
                indexOne : indexOfFirstPost,
                indexLast : indexOfLastPost
            })
        })
        .catch(()=>{
            setRequest({
                loading:false,
                data:null,
                totalPosts:null,
                error:true,
                posts:null
            })
        })
    },[currentPage])
    
    return request
}