import { React, useState } from "react";
import Pagination from "react-js-pagination";


const PaginationComponent= ({postPerPage, totalPosts, paginate}) => {
    const pageNumbers = []
    //const [activePage, setActivePage] = useState(1)
    for(let i = 0; i< Math.ceil(totalPosts/postPerPage);i++)
        pageNumbers.push(i+1)
        
    return (
        <div >
            <ul className= "flex justify-center">
                {pageNumbers.map(number=>(
                    <li key={number} className= "flex-col border border-gray-600 px-2 py-0 mb-5 bg-gray-200 hover:bg-gray-500 hover:text-white cursor-pointer text-xl">
                        <a onClick={()=>paginate(number)} href="#">
                            {number}
                        </a>
                    </li>
                ))}
            </ul>
        </div>
        );
}

export default PaginationComponent
